.button-container {
  position: relative;
  display: inline-block;
  width: 120px;
  height: 36px;
  color: #fff;
  background: #1890ff;
  border: 1px solid #1890ff;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}
.button-container span {
  line-height: 1.5;
}
.button-container:active::after {
  opacity: 0.3;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0s;
  transition: 0s;
}
.button-container:hover {
  background: #40a9ff;
  border-color: #40a9ff;
}
.button-container::after {
  content: "";
  display: block;
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  pointer-events: none;
  background-color: #333;
  background-repeat: no-repeat;
  background-position: 50%;
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
